var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"content-center"},[_c('div',{staticClass:"container"},[_c('h1',{staticClass:"title"},[_vm._v("Política de Privacidad")]),_c('div',{staticClass:"text-center"},[_c('a',{staticClass:"btn btn-primary btn-icon btn-round",attrs:{"href":"https://web.facebook.com/TedescoDev/","target":"_blank"}},[_c('i',{staticClass:"fab fa-facebook-square"})]),_c('a',{staticClass:"btn btn-primary btn-icon btn-round",attrs:{"href":"https://twitter.com/TedescoDev","target":"_blank"}},[_c('i',{staticClass:"fab fa-twitter"})]),_c('a',{staticClass:"btn btn-primary btn-icon btn-round",attrs:{"href":"https://www.instagram.com/tedescodev/","target":"_blank"}},[_c('i',{staticClass:"fab fa-instagram"})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"section section-about-us"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-12 ml-auto mr-auto text-center"},[_c('h2',{staticClass:"title"},[_vm._v("Tedesco Dev.")])]),_c('div',{staticClass:"col-md-6 ml-auto mr-auto "},[_c('h3',{staticClass:"title"},[_vm._v("I. POLÍTICA DE PRIVACIDAD Y PROTECCIÓN DE DATOS")]),_c('h5',{staticClass:"description"},[_vm._v("\n            Respetando lo establecido en la legislación vigente, \n            TEDESCO DEV (en adelante, también Sitio Web) se compromete a \n            adoptar las medidas técnicas y organizativas necesarias, \n            según el nivel de seguridad adecuado al riesgo de los datos recogidos.\n          ")]),_c('h5',{staticClass:"description"},[_c('h5',[_vm._v("Leyes que incorpora esta política de privacidad:")]),_vm._v("\n            Esta política de privacidad está adaptada a la normativa española \n            y europea vigente en materia de protección de datos personales en internet. \n            En concreto, la misma respeta las siguientes normas:\n          ")]),_c('h5',{staticClass:"description"},[_c('ul',[_c('li',[_vm._v("\n                El Reglamento (UE) 2016/679 del Parlamento Europeo y del Consejo, \n                de 27 de abril de 2016, relativo a la protección de las personas \n                físicas en lo que respecta al tratamiento de datos personales y a \n                la libre circulación de estos datos (RGPD).\n              ")]),_c('li',[_vm._v("\n                El Real Decreto-ley 5/2018, de 27 de julio, de medidas urgentes \n                para la adaptación del Derecho español a la normativa de la Unión Europea\n                en materia de protección de datos.\n              ")]),_c('li',[_vm._v("\n                La Ley Orgánica 15/1999, de 13 de diciembre, de Protección de Datos de \n                Carácter Personal (LOPD).\n              ")]),_c('li',[_vm._v("\n                El Real Decreto 1720/2007, de 21 de diciembre, por el que se aprueba \n                el Reglamento de desarrollo de la Ley Orgánica 15/1999, de 13 de diciembre,\n                de Protección de Datos de Carácter Personal (RDLOPD).\n              ")]),_c('li',[_vm._v("\n                La Ley 34/2002, de 11 de julio, de Servicios de la Sociedad de la Información\n                y de Comercio Electrónico (LSSI-CE).\n              ")])])]),_c('h5',{staticClass:"description"},[_c('h5',[_vm._v("Identidad del responsable del tratamiento de los datos personales")]),_vm._v("\n            El responsable del tratamiento de los datos personales recogidos en \n            TEDESCO DEV es: Héctor Daniel Tedesco, con NIF: X7513667G (en adelante, \n            responsable del tratamiento). Sus datos de contacto son los siguientes:\n            Dirección: \t\n            C/ Tulipán 37, Mijas Costa, Málaga\t\n            Teléfono de contacto: +34622929010\t\n            Email de contacto: info@tedesco.es\n            Delegado de Protección de Datos (DPD)\n          ")]),_c('h5',{staticClass:"description"},[_vm._v("\n            El Delegado de Protección de Datos (DPD, o DPO por sus siglas en inglés) es el encargado\n            de velar por el cumplimiento de la normativa de protección de datos a la cual se encuentra\n            sujeta TEDESCO DEV. El Usuario puede contactar con el DPD designado por el Responsable del\n            tratamiento utilizando los siguientes datos de contacto: direccion@tedesco.es.\n          ")]),_c('h5',{staticClass:"description"},[_c('h5',[_vm._v("Registro de Datos de Carácter Personal")]),_vm._v("\n            Los datos personales recabados por TEDESCO DEV, mediante los formularios extendidos en sus\n            páginas, serán introducidos en un fichero automatizado bajo la responsabilidad del \n            Responsable del tratamiento, y debidamente declarado e inscrito en el Registro General de\n            la Agencia de Protección de Datos que se puede consultar en el sitio web de la \n            Agencia Española de Protección de Datos (http://www.agpd.es), con la finalidad de poder\n            facilitar, agilizar y cumplir los compromisos establecidos entre TEDESCO DEV y el Usuario\n            o el mantenimiento de la relación que se establezca en los formularios que este rellene, \n            o para atender una solicitud o consulta del mismo.\n          ")]),_c('h5',{staticClass:"description"},[_c('h5',[_vm._v("Principios aplicables al tratamiento de los datos personales")]),_vm._v("\n            El tratamiento de los datos personales del Usuario se someterá a los siguientes principios recogidos en el artículo 5 del RGPD:\n            "),_c('ul',[_c('li',[_vm._v("Principio de licitud, lealtad y transparencia: se requerirá en todo momento el consentimiento del Usuario previa información completamente transparente de los fines para los cuales se recogen los datos personales.")]),_c('li',[_vm._v("Principio de limitación de la finalidad: los datos personales serán recogidos con fines determinados, explícitos y legítimos.")]),_c('li',[_vm._v("Principio de minimización de datos: los datos personales recogidos serán únicamente los estrictamente necesarios en relación con los fines para los que son tratados.")]),_c('li',[_vm._v("Principio de exactitud: los datos personales deben ser exactos y estar siempre actualizados.")]),_c('li',[_vm._v("Principio de limitación del plazo de conservación: los datos personales solo serán mantenidos de forma que se permita la identificación del Usuario durante el tiempo necesario para los fines de su tratamiento.")]),_c('li',[_vm._v("Principio de integridad y confidencialidad: los datos personales serán tratados de manera que se garantice su seguridad y confidencialidad.")]),_c('li',[_vm._v("Principio de responsabilidad proactiva: el Responsable del tratamiento será responsable de asegurar que los principios anteriores se cumplen.")])])]),_c('h5',{staticClass:"description"},[_c('h5',[_vm._v("Categorías de datos personales")]),_vm._v("\n            Las categorías de datos que se tratan en TEDESCO DEV son únicamente datos identificativos.\n            En ningún caso, se tratan categorías especiales de datos personales en el sentido del \n            artículo 9 del RGPD.\n          ")]),_c('h5',{staticClass:"description"},[_c('h5',[_vm._v("Base legal para el tratamiento de los datos personales")]),_vm._v("\n            La base legal para el tratamiento de los datos personales es el consentimiento. \n            TEDESCO DEV se compromete a recabar el consentimiento expreso y verificable del Usuario \n            para el tratamiento de sus datos personales para uno o varios fines específicos.\n            El Usuario tendrá derecho a retirar su consentimiento en cualquier momento. \n            Será tan fácil retirar el consentimiento como darlo. Como regla general, la retirada del\n            consentimiento no condicionará el uso del Sitio Web.\n            En las ocasiones en las que el Usuario deba o pueda facilitar sus datos a través de \n            formularios para realizar consultas, solicitar información o por motivos relacionados \n            con el contenido del Sitio Web, se le informará en caso de que la cumplimentación \n            de alguno de ellos sea obligatoria debido a que los mismos sean imprescindibles \n            para el correcto desarrollo de la operación realizada.\n          ")]),_c('h5',{staticClass:"description"},[_c('h5',[_vm._v("Fines del tratamiento a que se destinan los datos personales")]),_vm._v("\n            Los datos personales son recabados y gestionados por TEDESCO DEV con la finalidad de poder\n            facilitar, agilizar y cumplir los compromisos establecidos entre el Sitio Web y el Usuario\n            o el mantenimiento de la relación que se establezca en los formularios que este último \n            rellene o para atender una solicitud o consulta.\n            Igualmente, los datos podrán ser utilizados con una finalidad comercial de personalización,\n            operativa y estadística, y actividades propias del objeto social de TEDESCO DEV, \n            así como para la extracción, almacenamiento de datos y estudios de marketing para adecuar\n            el Contenido ofertado al Usuario, así como mejorar la calidad, funcionamiento y navegación\n            por el Sitio Web.\n            En el momento en que se obtengan los datos personales, se informará al Usuario acerca del \n            fin o fines específicos del tratamiento a que se destinarán los datos personales; es decir,\n            del uso o usos que se dará a la información recopilada..\n          ")]),_c('h5',{staticClass:"description"},[_c('h5',[_vm._v("Períodos de retención de los datos personales")]),_vm._v(" \n            Los datos personales solo serán retenidos durante el tiempo mínimo necesario para los fines\n            de su tratamiento y, en todo caso, únicamente durante el siguiente plazo: 2, o hasta que\n            el Usuario solicite su supresión.\n            En el momento en que se obtengan los datos personales, se informará al Usuario acerca del\n            plazo durante el cual se conservarán los datos personales o, cuando eso no sea posible, \n            los criterios utilizados para determinar este plazo.\n            Destinatarios de los datos personales \n            Los datos personales del Usuario no serán compartidos con terceros.\n            En cualquier caso, en el momento en que se obtengan los datos personales, se informará \n            al Usuario acerca de los destinatarios o las categorías de destinatarios de los datos \n            personales.\n          ")]),_c('h5',{staticClass:"description"},[_c('h5',[_vm._v("Datos personales de menores de edad")]),_vm._v("\n            Respetando lo establecido en los artículos 8 del RGPD y 13 del RDLOPD, solo los mayores de\n            14 años podrán otorgar su consentimiento para el tratamiento de sus datos personales de \n            forma lícita por TEDESCO DEV. Si se trata de un menor de 14 años, será necesario el \n            consentimiento de los padres o tutores para el tratamiento, y este solo se considerará \n            lícito en la medida en la que los mismos lo hayan autorizado.\n          ")]),_c('h5',{staticClass:"description"},[_c('h5',[_vm._v("Secreto y seguridad de los datos personales")]),_vm._v("\n            TEDESCO DEV se compromete a adoptar las medidas técnicas y organizativas necesarias, \n            según el nivel de seguridad adecuado al riesgo de los datos recogidos, de forma que se \n            garantice la seguridad de los datos de carácter personal y se evite la destrucción, \n            pérdida o alteración accidental o ilícita de datos personales transmitidos, conservados \n            o tratados de otra forma, o la comunicación o acceso no autorizados a dichos datos. \n            El Sitio Web cuenta con un certificado SSL (Secure Socket Layer), que asegura que los \n            datos personales se transmiten de forma segura y confidencial, al ser la transmisión de \n            los datos entre el servidor y el Usuario, y en retroalimentación, totalmente cifrada o \n            encriptada.\n          ")]),_c('h5',{staticClass:"description"},[_vm._v("\n            Sin embargo, debido a que TEDESCO DEV no puede garantizar la inexpugabilidad de internet \n            ni la ausencia total de hackers u otros que accedan de modo fraudulento a los datos \n            personales, el responsable del tratamiento se compromete a comunicar al Usuario sin \n            dilación indebida cuando ocurra una violación de la seguridad de los datos personales \n            que sea probable que entrañe un alto riesgo para los derechos y libertades de las \n            personas físicas. Siguiendo lo establecido en el artículo 4 del RGPD, se entiende por \n            violación de la seguridad de los datos personales toda violación de la seguridad que \n            ocasione la destrucción, pérdida o alteración accidental o ilícita de datos personales \n            transmitidos, conservados o tratados de otra forma, o la comunicación o acceso no \n            autorizados a dichos datos.\n          ")]),_c('h5',{staticClass:"description"},[_vm._v("\n            Los datos personales serán tratados como confidenciales por el Responsable del tratamiento,\n            quien se compromete a informar de y a garantizar por medio de una obligación legal o \n            contractual que dicha confidencialidad sea respetada por sus empleados, asociados, y \n            toda persona a la cual le haga accesible la información.\n          ")])]),_c('div',{staticClass:"col-md-6 ml-auto mr-auto"},[_c('h5',{staticClass:"description"},[_c('h5',[_vm._v("Derechos derivados del tratamiento de los datos personales.")]),_vm._v("\n            El Usuario tiene sobre TEDESCO DEV y podrá, por tanto, ejercer frente al \n            responsable del tratamiento los siguientes derechos reconocidos en el RGPD:\n          ")]),_c('h5',{staticClass:"description"},[_c('b',[_vm._v("Derecho de acceso:")]),_vm._v(" Es el derecho del Usuario a obtener confirmación de si \n            TEDESCO DEV está tratando o no sus datos personales y, en caso afirmativo, \n            obtener información sobre sus datos concretos de carácter personal y del \n            tratamiento que TEDESCO DEV haya realizado o realice, así como, entre otra, \n            de la información disponible sobre el origen de dichos datos y los destinatarios\n            de las comunicaciones realizadas o previstas de los mismos. \n          ")]),_c('h5',{staticClass:"description"},[_c('b',[_vm._v("Derecho de rectificación:")]),_vm._v(" Es el derecho del Usuario a que se modifiquen sus \n            datos personales que resulten ser inexactos o, teniendo en cuenta los fines del \n            tratamiento, incompletos.\n          ")]),_c('h5',{staticClass:"description"},[_c('b',[_vm._v("Derecho de supresión (\"el derecho al olvido\"):")]),_vm._v(" Es el derecho del Usuario, siempre \n            que la legislación vigente no establezca lo contrario, a obtener la supresión de sus datos\n            personales cuando estos ya no sean necesarios para los fines para los cuales fueron \n            recogidos o tratados; el Usuario haya retirado su consentimiento al tratamiento y este no\n            cuente con otra base legal; el Usuario se oponga al tratamiento y no exista otro motivo\n            legítimo para continuar con el mismo; los datos personales hayan sido tratados ilícitamentemente;\n            los datos personales deban suprimirse en cumplimiento de una obligación legal; o los datos \n            personales hayan sido obtenidos producto de una oferta directa de servicios de la sociedad\n            de la información a un menor de 14 años. Además de suprimir los datos, el Responsable del\n            tratamiento, teniendo en cuenta la tecnología disponible y el coste de su aplicación,\n            deberá adoptar medidas razonables para informar a los responsables que estén tratando los\n            datos personales de la solicitud del interesado de supresión de cualquier enlace a esos datos\n            personales. \n          ")]),_c('h5',{staticClass:"description"},[_c('b',[_vm._v("Derecho a la limitación del tratamiento:")]),_vm._v(" Es el derecho del Usuario a limitar el tratamiento\n            de sus datos personales. El Usuario tiene derecho a obtener la limitación del tratamiento cuando \n            impugne la exactitud de sus datos personales; el tratamiento sea ilícito; el Responsable del\n            tratamiento ya no necesite los datos personales, pero el Usuario lo necesite para hacer reclamaciones;\n            y cuando el Usuario se haya opuesto al tratamiento. \n          ")]),_c('h5',{staticClass:"description"},[_c('b',[_vm._v("Derecho a la portabilidad de los datos:")]),_vm._v(" En caso de que el tratamiento se efectúe por medios\n            automatizados, el Usuario tendrá derecho a recibir del Responsable del tratamiento sus datos\n            personales en un formato estructurado, de uso común y lectura mecánica, y a transmitirlos a otro\n            responsable del tratamiento. Siempre que sea técnicamente posible, el Responsable del tratamiento\n            transmitirá directamente los datos a ese otro responsable. \n          ")]),_c('h5',{staticClass:"description"},[_c('b',[_vm._v("Derecho de oposición:")]),_vm._v(" Es el derecho del Usuario a que no se lleve a cabo el tratamiento\n            de sus datos de carácter personal o se cese el tratamiento de los mismos por parte de TEDESCO DEV. \n          ")]),_c('h5',{staticClass:"description"},[_vm._v("\n            Derecho a no ser a no ser objeto de una decisión basada únicamente en el tratamiento automatizado,\n            incluida la elaboración de perfiles: Es el derecho del Usuario a no ser objeto de una decisión\n            individualizada basada únicamente en el tratamiento automatizado de sus datos personales,\n            incluida la elaboración de perfiles, existente salvo que la legislación vigente establezca lo contrario. \n          ")]),_c('h5',{staticClass:"description"},[_vm._v("\n            Así pues, el Usuario podrá ejercitar sus derechos mediante comunicación escrita dirigida al Responsable del tratamiento con la referencia \"RGPD-www.tedesco.es\", especificando:\n            "),_c('ul',[_c('li',[_vm._v("Nombre, apellidos del Usuario y copia del DNI. En los casos en que se admita la representación, será también necesaria la identificación por el mismo medio de la persona que representa al Usuario, así como el documento acreditativo de la representación. La fotocopia del DNI podrá ser sustituida, por cualquier otro medio válido en derecho que acredite la identidad.")]),_c('li',[_vm._v("Petición con los motivos específicos de la solicitud o información a la que se quiere acceder.")]),_c('li',[_vm._v("Domicilio a efecto de notificaciones.")]),_c('li',[_vm._v("Fecha y firma del solicitante.")]),_c('li',[_vm._v("Todo documento que acredite la petición que formula.")])]),_vm._v("\n            Esta solicitud y todo otro documento adjunto podrá enviarse a la siguiente dirección y/o correo electrónico:\n            Dirección postal: \t\n            C/ Tulipán 37, Mijas Costa, Málaga\t\n            Correo electrónico: direccion@tedesco.es\n            Enlaces a sitios web de terceros\n            El Sitio Web puede incluir hipervínculos o enlaces que permiten acceder a páginas web de terceros distintos de TEDESCO DEV, y que por tanto no son operados por TEDESCO DEV. Los titulares de dichos sitios web dispondrán de sus propias políticas de protección de datos, siendo ellos mismos, en cada caso, responsables de sus propios ficheros y de sus propias prácticas de privacidad.\n            Reclamaciones ante la autoridad de control\n            En caso de que el Usuario considere que existe un problema o infracción de la normativa vigente en la forma en la que se están tratando sus datos personales, tendrá derecho a la tutela judicial efectiva y a presentar una reclamación ante una autoridad de control, en particular, en el Estado en el que tenga su residencia habitual, lugar de trabajo o lugar de la supuesta infracción. En el caso de España, la autoridad de control es la Agencia Española de Protección de Datos (http://www.agpd.es). \n          ")]),_c('h3',{staticClass:"title"},[_vm._v("II. POLÍTICA DE COOKIES")]),_c('h5',{staticClass:"description"},[_vm._v("\n            El acceso a este Sitio Web puede implicar la utilización de cookies. Las cookies son pequeñas cantidades de información que se almacenan en el navegador utilizado por cada Usuario —en los distintos dispositivos que pueda utilizar para navegar— para que el servidor recuerde cierta información que posteriormente y únicamente el servidor que la implementó leerá. Las cookies facilitan la navegación, la hacen más amigable, y no dañan el dispositivo de navegación.\n            Las cookies son procedimientos automáticos de recogida de información relativa a las preferencias determinadas por el Usuario durante su visita al Sitio Web con el fin de reconocerlo como Usuario, y personalizar su experiencia y el uso del Sitio Web, y pueden también, por ejemplo, ayudar a identificar y resolver errores.\n            La información recabada a través de las cookies puede incluir la fecha y hora de visitas al Sitio Web, las páginas visionadas, el tiempo que ha estado en el Sitio Web y los sitios visitados justo antes y después del mismo. Sin embargo, ninguna cookie permite que esta misma pueda contactarse con el número de teléfono del Usuario o con cualquier otro medio de contacto personal. Ninguna cookie puede extraer información del disco duro del Usuario o robar información personal. La única manera de que la información privada del Usuario forme parte del archivo Cookie es que el usuario dé personalmente esa información al servidor.\n            Las cookies que permiten identificar a una persona se consideran datos personales. Por tanto, a las mismas les será de aplicación la Política de Privacidad anteriormente descrita. En este sentido, para la utilización de las mismas será necesario el consentimiento del Usuario. Este consentimiento será comunicado, en base a una elección auténtica, ofrecido mediante una decisión afirmativa y positiva, antes del tratamiento inicial, removible y documentado.\n            Cookies propias\n            Son aquellas cookies que son enviadas al ordenador o dispositivo del Usuario y gestionadas exclusivamente por TEDESCO DEV para el mejor funcionamiento del Sitio Web. La información que se recaba se emplea para mejorar la calidad del Sitio Web y su Contenido y su experiencia como Usuario. Estas cookies permiten reconocer al Usuario como visitante recurrente del Sitio Web y adaptar el contenido para ofrecerle contenidos que se ajusten a sus preferencias.\n            La(s) entidad(es) encargada(s) del suministro de cookies podrá(n) ceder esta información a terceros, siempre y cuando lo exija la ley o sea un tercero el que procese esta información para dichas entidades. \n            Cookies de redes sociales\n          ")]),_c('h5',{staticClass:"description"},[_vm._v("\n            TEDESCO DEV incorpora plugins de redes sociales, que permiten acceder a las mismas a partir del \n            Sitio Web. Por esta razón, las cookies de redes sociales pueden almacenarse en el navegador del \n            Usuario. Los titulares de dichas redes sociales disponen de sus propias políticas de protección \n            de datos y de cookies, siendo ellos mismos, en cada caso, responsables de sus propios ficheros \n            y de sus propias prácticas de privacidad. El Usuario debe referirse a las mismas para informarse \n            acerca de dichas cookies y, en su caso, del tratamiento de sus datos personales. Únicamente a \n            título informativo se indican a continuación los enlaces en los que se pueden consultar dichas \n            políticas de privacidad y/o de cookies: